@import url(https://fonts.googleapis.com/css?family=Quicksand:400,700);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{background:#f0f8ea}header{background:#e54b4b;color:#ebebd3;padding:1em 0;position:relative;font-family:"Quicksand", sans-serif;z-index:100}header::after{content:"";clear:both;display:block}header .container{width:95%;max-width:1000px;margin:0 auto}header .logo{float:left;font-size:1rem;margin:0;font-weight:700;text-transform:uppercase;color:white}header .logo span{font-weight:400}header .site-nav{position:absolute;background:#464655;top:100%;right:0;transition:all 0.5s linear 0s;-webkit-clip-path:circle(0px at top right);clip-path:circle(0px at top right)}header .site-nav ul{list-style:none;margin:0;padding:0}header .site-nav ul li{border-bottom:1px solid #575766}header .site-nav ul li:last-child{border-bottom:none}header .site-nav a{color:#ebebd3;display:block;padding:1.5em 4em 1.5em 1.5em;text-transform:uppercase;text-decoration:none;font-weight:400}header .site-nav a:hover,header .site-nav a:focus{background:#e4b363;color:#464655}header .site-nav--icon{display:inline-block;margin-right:1em;font-size:1.5em;width:1.1em;text-align:right;color:rgba(255,255,255,0.4)}header .site-nav--open{-webkit-clip-path:circle(150% at top right);clip-path:circle(150% at top right);transition:all 0.5s linear 0s}header .site-nav--close{max-height:0px;transition:max-height 0.5s linear 0s}header .menu-toggle{padding:1em;position:absolute;top:0.5em;right:0.5em;cursor:pointer}header .menu-toggle .hamburger{background:#ebebd3;height:3px;width:1.75em;border-radius:3px;transition:all 0.2s linear}header .menu-toggle .hamburger::before,header .menu-toggle .hamburger::after{content:"";display:block;background:#ebebd3;height:3px;width:1.75em;border-radius:3px}header .menu-toggle .hamburger::before{transform:translateY(-6px);transition:all 0.2s linear}header .menu-toggle .hamburger::after{transform:translateY(3px);transition:all 0.2s linear}header .menu-toggle .hamburger--open{transform:rotate(45deg);transition:all 0.2s linear}header .menu-toggle .hamburger--open::before{opacity:0}header .menu-toggle .hamburger--open::after{transform:translateY(-2px) rotate(-90deg);transition:all 0.2s linear}@media (min-width: 800px){header .site-nav{height:auto;position:relative;background:transparent;float:right;-webkit-clip-path:initial;clip-path:initial;height:auto;max-height:500px;transition:max-height 0.3s linear 0s}header .site-nav ul{margin:0;padding:0;list-style:none}header .site-nav ul li{display:inline-block;border:none}header .site-nav a{color:#ebebd3;display:block;padding:0;text-transform:uppercase;text-decoration:none;margin-left:3em}header .site-nav a:hover,header .site-nav a:focus{background:transparent;color:#464655}.menu-toggle{display:none}}

html, body {
  width: 100vw;
  height: 100vh;
  padding: 0;
  margin: 0;
  outline: 0;
}

* {
  margin: 0;
  padding: 0;
  outline: 0;
  box-sizing: border-box;
}


.app {
  width: 100vw;
  height: 100vh;
  background: #fff;
  overflow-x: hidden;

  -ms-overflow-style: none;  /* IE and Edge */
  scrollbar-width: none;  /* Firefox */

}

.nav-bar {
  height: 56px;
}
.spinner-wrapper{width:100%;height:100%;margin:0;padding:0;outline:0;display:flex;align-items:center;justify-content:center;background-color:black}.spinner{max-width:200px;animation:2s linear infinite svg--animation}circle{fill:transparent;stroke:#e54b4b;stroke-width:10;stroke-linecap:round;transform-origin:50% 50%;stroke-dasharray:283;animation:1.4s ease-in-out infinite both circle--animation}@keyframes circle--animation{0%,25%{stroke-dashoffset:280;transform:rotate(0)}50%,75%{stroke-dashoffset:75;transform:rotate(45deg)}100%{stroke-dashoffset:280;transform:rotate(360deg)}}@keyframes svg--animation{0%{transform:rotateZ(0deg)}100%{transform:rotateZ(360deg)}}

